import React, { useEffect } from "react";

import "./MMenu.scss";

import settingsSVG from "./img/setting.svg";

import {
  useDisclosure,
  Modal,
  ModalOverlay,
  ModalHeader,
  ModalFooter,
  ModalBody,
  ModalCloseButton,
  ModalContent,
  Tabs,
  Tab,
  TabList,
  TabPanels,
  TabPanel,
  Menu,
  MenuButton,
  MenuList,
  MenuItem,
  Portal,
} from "@chakra-ui/react";
import { Link } from "react-router-dom";

function MMenu() {
  const { isOpen, onOpen, onClose } = useDisclosure();
  useEffect(() => {
    onOpen("true");
  }, []); // eslint-disable-line

  return (
    <>
      <Modal
        scrollBehavior="inside"
        size="xl"
        closeOnOverlayClick={false}
        isOpen={isOpen}
        onClose={onClose}
      >
        <ModalOverlay />
        <ModalContent>
          <ModalHeader style={{ textAlign: "center" }}>
                 :  
             - 
          </ModalHeader>
          <ModalCloseButton />
          <ModalBody pb={6}></ModalBody>
          <Tabs isFitted>
            <TabList>
              <Tab></Tab>
              <Tab>   </Tab>
              <Tab></Tab>
            </TabList>

            <TabPanels>
              <TabPanel>
                <div className="menu-training-tab">
                  <Menu>
                    <MenuButton className="menu-training-card">
                       1
                    </MenuButton>
                    <Portal>
                      <MenuList zIndex="10000">
                        <MenuItem>
                          <Link to="/training/1l1">  </Link>1
                        </MenuItem>
                        <MenuItem>
                          <Link to="/training/1l2"> 2</Link>
                        </MenuItem>
                        <MenuItem>
                          <Link to="/training/1l3"> 3</Link>
                        </MenuItem>
                      </MenuList>
                    </Portal>
                  </Menu>
                  <Menu>
                    <MenuButton className="menu-training-card">
                       2
                    </MenuButton>
                    <Portal>
                      <MenuList zIndex="10000">
                        <MenuItem>
                          <Link to="/training/2l1"> 1</Link>
                        </MenuItem>
                        <MenuItem>
                          <Link to="/training/2l2"> 2</Link>
                        </MenuItem>
                        <MenuItem>
                          <Link to="/training/2l3"> 3</Link>
                        </MenuItem>
                      </MenuList>
                    </Portal>
                  </Menu>
                  <Menu>
                    <MenuButton className="menu-training-card">
                       3
                    </MenuButton>
                    <Portal>
                      <MenuList zIndex="10000">
                        <MenuItem>
                          <Link to="/training/3l1"> 1</Link>
                        </MenuItem>
                        <MenuItem>
                          <Link to="/training/3l2"> 2</Link>
                        </MenuItem>
                        <MenuItem>
                          <Link to="/training/3l3"> 3</Link>
                        </MenuItem>
                      </MenuList>
                    </Portal>
                  </Menu>
                  <Menu>
                    <MenuButton className="menu-training-card">
                       4
                    </MenuButton>
                    <Portal>
                      <MenuList zIndex="10000">
                        <MenuItem>
                          <Link to="/training/4l1"> 1</Link>
                        </MenuItem>
                        <MenuItem>
                          <Link to="/training/4l2"> 2</Link>
                        </MenuItem>
                        <MenuItem>
                          <Link to="/training/4l3"> 3</Link>
                        </MenuItem>
                      </MenuList>
                    </Portal>
                  </Menu>
                </div>
              </TabPanel>
              <TabPanel>
                <p>two!</p>
              </TabPanel>
              <TabPanel>
                <p>three!</p>
              </TabPanel>
            </TabPanels>
          </Tabs>
          <ModalFooter></ModalFooter>
        </ModalContent>
      </Modal>
      <img
        onClick={onOpen}
        src={settingsSVG}
        className="absolute menu-training__btn"
        alt=""
      />
    </>
  );
}

export default MMenu;
